*{
  box-sizing: border-box;
}
::-webkit-scrollbar {
  display: none;
}
body{
background: rgb(0,0,0);
background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(125,125,125,1) 47%, rgba(255,255,255,1) 100%);
  color: #ffffff;
}

.searchInputs {
  /* margin-top: 55px;
  margin-bottom: 55px; */
  margin: 60px 60px;
  margin-left: 30%;
  display: flex;
  width: 40%;
  text-align: center;
}
.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-top-left-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  width: 90%;
}
.ico{
  position: relative;
  right: 130%;
  top: 9%;
  height: 30px;
  width: 30px;
}
.searchIcon{
  height: 60px;
  width: 50px;
  background-color: white;
  display: grid;
  place-items: center;
}
h1{
  display: block;
  text-align: center;
}
.movie-app .row
{
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  display: flex;
  flex-wrap: nowrap;
}
.cover {
  padding: 0px 60px;
  position: relative;
}
.left {
  position: absolute;
  border-radius: 10px;
  left: 1%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.right {
  position: absolute;
  right: 1%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.image-container{
  position: relative;
  transition: transform 0.2s;
}
.image-container:hover
{
  transform: scale(1.1);
}
.image-container:hover .overlay{
  opacity: 1;
}
.overlay:hover{
  cursor: pointer;
}
.overlay{
  opacity: 0;
  position: absolute;
  bottom: 0;
  font-size: 24px;
  background: rgba(0, 0, 0, 0.8);
	width: 100%;
  transition: 0.5s ease;
  padding: 20px;
  text-align: center;
}